.parent-container {
  height: 100vh;
}
.parts {
  height: 100%;
  display: flex;
}
.part1 {
  height: 100%;
  width: 50%;
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.part2 {
  height: 100%;
  width: 50%;
  background: #0f0c29; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #24243e,
    #302b63,
    #0f0c29
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #24243e,
    #302b63,
    #0f0c29
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.part1text {
  color: wheat;
  text-align: center;
  margin-top: 20%;
}
.lottie {
  background: transparent;
  align-items: center;
  justify-content: center;
}
.part2text {
  color: wheat;
  text-align: center;
  margin-bottom: 30px;
}
.part2src {
  margin-top: 20%;
}
.seprator-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: wheat;
}
.seprator-text span {
  writing-mode: vertical-lr;
  text-orientation: upright;
  font-size: 45px;
  font-weight: 60;
}
